import {Injectable} from '@angular/core';
import {Http, Response, RequestOptions, Headers} from '@angular/http';
import {Router} from '@angular/router';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class DataService {
  constructor(private http: Http) {}

  private ROOT_URL = 'https://api.github.com';

  private results = new BehaviorSubject([]);
  private repos = new BehaviorSubject([]);

  getResults$() {
    return this.results.asObservable();
  }

  getUserRepos$() {
    return this.repos.asObservable();
  }

  // Get Github repositories according to search parameters
  getResults(params) {
    return this.http
      .get(`${this.ROOT_URL}/search/repositories?q=${params}`)
      .map(this.extractData, this)
      .subscribe(response => this.results.next(response), this.handleError);
  }

  // Get Github repositories according to search parameters
  getUserInfo(username) {
    return this.http
      .get(`${this.ROOT_URL}/users/${username}`)
      .toPromise()
      .then(res => this.extractData(res))
      .catch(this.handleError);
  }

  // Get Github repositories according to search parameters
  getUserRepos(username) {
    return this.http
      .get(`${this.ROOT_URL}/users/${username}/repos`)
      .map(this.extractData, this)
      .subscribe(response => this.repos.next(response), this.handleError);
  }

  // Handler to extract data response
  public extractData(res: Response) {
    const body = res.json();
    return body || {};
  }

  // Error Handling Mechanism
  public handleError = (error: Response | any) => {
    return error.json();
  };
}



