import {Injectable} from '@angular/core';
import {
  Router,
  Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot,
} from '@angular/router';
import {DataService} from '../providers/data.service';

@Injectable()
export class ProfileGuard implements Resolve<any> {
  constructor(private router: Router, private dataService: DataService) {}
  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (route.params['username']) {
      // Check if the user exists. If yes, navigate. If no, return to list page.
      return this.dataService.getUserInfo(route.params['username']).then(data => {
        if (!data) {
          this.router.navigate(['search-results']);
        } else {
          this.dataService.getUserRepos(route.params['username']);
          return data;
        }
      });
    } else {
      this.router.navigate(['search-results']);
      return null;
    }
  }
}



