import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';

import {DataService} from '../../providers/data.service';

@Component({
  selector: 'app-search-results',
  templateUrl: './search-results.component.html',
  styleUrls: ['./search-results.component.scss'],
})
export class SearchResultsComponent implements OnInit {
  private totalCount;
  private loadingData = true;
  private results = [];
  private searchParams;
  private pageOfItems;

  constructor(
    private dataService: DataService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    dataService.getResults$().subscribe(response => {
      this.totalCount = response['total_count'];
      this.results = response['items'];
      this.loadingData = false;
    });
  }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.searchParams = params.q;
      if (!this.results) {
        this.dataService.getResults(this.searchParams);
      }
    });
  }

  getSearchResults(params) {
    if (params) {
      this.dataService.getResults(params);
      this.router.navigate(['search-results'], {queryParams: {q: params}});
    }
  }

  // Navigate to view profile page
  viewProfile(username) {
    this.router.navigate(['view-profile', username]);
  }

  // Paginator Events
  onChangePage(pageOfItems: Array<any>) {
    // update current page of items
    this.pageOfItems = pageOfItems;
  }
}



